import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useQuery } from '@tanstack/react-query';
import { useFormik } from 'formik';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Divider from '@mui/material/Divider';
import LocalShippingIcon from '@mui/icons-material/LocalShipping';
import FactoryIcon from '@mui/icons-material/Factory';
import PersonIcon from '@mui/icons-material/Person';

import StopsForm from './stops';

import {
  Loader,
  SearchField,
  SearchMultiField,
  SelectMultiField,
  TextField,
  SelectField,
} from '../components';

import { CustomersQuery, LoadQuery, TrucksQuery, UsersQuery } from '../api';
import { useSnackbar } from '../hooks';
import {
  CreateLoad,
  User,
  Truck,
  TruckTypes,
  LoadStatus,
  LoadStatuses,
  Customer,
} from '../utils/dataTypes';
import FileList from '../components/filesList';
import { cleanObject } from '../utils/queryHelpers';

const startValues = {
  status: 'Available' as LoadStatus,
  ref: [],
  stops: [],
  weight: undefined,
  truckType: [],
  rate: undefined,
  totalCharges: undefined,
  currency: 'USD',
  bookedByUser: null,
  bookedByCompany: undefined,
  assignTo: [],
  checkInAs: '4ULogistics',
  truck: null,
  bookedWith: null,
};

export default function ViewLoadForm() {
  const [initialValues, setInitialValues] =
    React.useState<CreateLoad>(startValues);
  const { pathname } = useLocation();
  const navigate = useNavigate();
  const setSnackbarMessage = useSnackbar();

  const {
    isLoading: loading,
    isFetching: fetching,
    data,
  } = useQuery({
    queryKey: ['load', pathname.split('/')[3]],
    queryFn: LoadQuery,
    keepPreviousData: false,
    retry: false,
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const formik = useFormik<CreateLoad>({
    initialValues: {
      ...initialValues,
      ...data,
    },
    enableReinitialize: true,
    onSubmit: () => {
      return;
    },
  });

  React.useEffect(() => {
    setInitialValues({
      ...initialValues,
      ...cleanObject(data, Object.keys(initialValues)),
    });
  }, [data]);

  const handleClose = () => {
    navigate(-1);
  };

  const handleEdit = () => {
    navigate(pathname.replace('view', 'edit'), { replace: true });
  };

  return (
    <Box sx={{ height: '100%' }}>
      <form
        onSubmit={formik.handleSubmit}
        onReset={formik.handleReset}
        style={{ height: '100%' }}
      >
        <Box sx={{ p: 3, height: '100%' }}>
          <Divider />
          <Box
            key="controls"
            sx={{
              maxHeight: 'calc(100% - 70px)',
              overflowX: 'hidden',
              overflowY: 'scroll',
            }}
          >
            <Box key="topControls" sx={{ marginBottom: -1, p: 1 }}>
              <SelectField
                fieldName="status"
                label="Status"
                items={LoadStatuses}
                /*disabledItems={LoadStatuses.filter(
                (status) =>
                  status !== 'Available' && status !== 'Not Available',
              )}*/
                disabled
                formik={formik}
              />
              <TextField
                fieldName="ref"
                label="REF"
                multiline
                maxRows={3}
                disabled
                formik={formik}
              />
              <StopsForm fieldName="stops" formik={formik} disabled />
            </Box>
            <Box
              key="centerControls"
              sx={{ display: 'inline-flex', width: '100%' }}
            >
              <Box key="left" sx={{ p: 1, width: '50%' }}>
                <TextField fieldName="weight" label="Weight" formik={formik} />
                <SelectMultiField
                  fieldName="truckType"
                  label="Truck Type"
                  items={TruckTypes}
                  disabled
                  formik={formik}
                />
                <SearchField<User>
                  fieldName="bookedByUser"
                  label="Booked By"
                  getSelectedLabel={(selectedItem) =>
                    `${selectedItem.fullName}`
                  }
                  getItemLabel={(item) => `${item.fullName}`}
                  getItemSecondLabel={(item) => `${item.jobTitle}`}
                  ListIcon={PersonIcon}
                  queryFn={UsersQuery}
                  orderby="fullName"
                  order="asc"
                  disabled
                  formik={formik}
                />
                <SearchField<Customer>
                  fieldName="bookedWith"
                  label="Booked With"
                  getSelectedLabel={(selectedItem) => `${selectedItem.name}`}
                  getItemLabel={(item) => `${item.name}`}
                  getItemSecondLabel={(item) => `${item.type}`}
                  ListIcon={FactoryIcon}
                  queryFn={CustomersQuery}
                  orderby="name"
                  order="asc"
                  disabled
                  formik={formik}
                />
                <SearchField<Truck>
                  fieldName="truck"
                  label="Truck"
                  getSelectedLabel={(selectedItem) =>
                    `${selectedItem.truckNumber}`
                  }
                  getItemLabel={(item) => `${item.truckNumber}`}
                  getItemSecondLabel={(item) =>
                    `${item.status}, ${item.licencePlate}`
                  }
                  ListIcon={LocalShippingIcon}
                  queryFn={TrucksQuery}
                  orderby="truckNumber"
                  order="asc"
                  disabled
                  formik={formik}
                />
              </Box>
              <Box key="right" sx={{ p: 1, width: '50%' }}>
                <TextField
                  fieldName="rate"
                  label="Rate"
                  number
                  disabled
                  formik={formik}
                />
                <TextField
                  fieldName="totalCharges"
                  label="Total Charges"
                  number
                  disabled
                  formik={formik}
                />
                <TextField
                  fieldName="currency"
                  label="Currency"
                  disabled
                  formik={formik}
                />
                <TextField
                  fieldName="checkInAs"
                  label="Check In As"
                  disabled
                  formik={formik}
                />
                <TextField
                  fieldName="bookedByCompany"
                  label="Booked By Company"
                  disabled
                  formik={formik}
                />
              </Box>
            </Box>
            <Box
              key="bottomControls"
              sx={{ p: 1, paddingBottom: 0, paddingTop: 0 }}
            >
              <SearchMultiField<User>
                fieldName="assignTo"
                label="Assign To"
                getSelectedLabel={(selectedItem) => `${selectedItem.fullName}`}
                getItemLabel={(item) => `${item.fullName}`}
                getItemSecondLabel={(item) => `${item.jobTitle}`}
                ListIcon={PersonIcon}
                queryFn={UsersQuery}
                orderby="fullName"
                order="asc"
                disabled
                formik={formik}
              />
              <Box key="spacer" sx={{ paddingBottom: 2, paddingTop: 2 }} />
              <FileList objectId={data?.id} objectType="Load" disabled />
            </Box>
          </Box>
          <Divider />
          <Box key="buttons" sx={{ paddingTop: 3, display: 'flex' }}>
            <Box key="left" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleEdit}
              >
                Edit
              </Button>
            </Box>
            <Box key="right" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleClose}
              >
                Cancel
              </Button>
            </Box>
          </Box>
        </Box>
      </form>
      <Loader visible={loading || fetching} />
    </Box>
  );
}
